  flex-direction: column;
  align-items: center;
  font-size: 23px;
  display: flex;
}

#contact-infos {
  margin-top: 7px;
  position: absolute;
  right: 50px;
}

#contact-infos p {
  margin-top: 2px;
  margin-bottom: 0;
}

#columns-wrapper {
  display: flex;
}

#column1, #column2 {
  margin-left: 30px;
}

#column1 {
  border-right: 1px solid;
  flex: 1;
